import React from "react"
import Fade from "react-reveal/Fade"
import data from "../data"
import Typical from 'react-typical'
import scrollTo from "gatsby-plugin-smoothscroll"
import styled, { keyframes } from "styled-components";
import { bounce } from 'react-animations';
import Swing from 'react-reveal/Swing';

const bounceAnimation = keyframes`${bounce}`;
const BounceDiv = styled.div`
animation: infinite 3.5s ${bounceAnimation};
`;

// Landing Page

const Header = () => {
  return (
    <div className="section" id="home">
      <div className="container">
        <div className="header-wrapper">
          <Fade bottom>
            <h2>
              Hi, I'm {data.name}{" "}
              <div className="emoji">
                <Swing count={3} duration={1500}>
                  
              </Swing>
              </div>


              <br></br>
              I am a
              <Typical
                steps={[' developer.', 2000, ' designer.', 2000, ' program manager.', 2000]}
                loop={Infinity}
                className="inline"
                wrapper="p"
              />
            </h2>
          </Fade>

          <Fade bottom cascade>
            <div className="heading-wrapper">
              <h1>
                {data.headerTagline[0]
                  ? data.headerTagline[0]
                  : "Building Applications"}
                <span role="img" aria-label="Emoji">
                  
                  </span>
              </h1>
              <h1>
                <br></br>
                {" "}
                {data.headerTagline[1]
                  ? data.headerTagline[1]
                  : "Designing Prototypes"}
                <span role="img" aria-label="Emoji">
                  
                  </span>
              </h1>
              <h1>
                <br></br>
                {" "}
                {data.headerTagline[2]
                  ? data.headerTagline[2]
                  : "Managing Projects"}
                <span role="img" aria-label="Emoji">
                  
                  </span>
              </h1>
            </div>
          </Fade>

          <Fade bottom>
            <br></br><br></br><br></br><br></br>

            <button onClick={() => scrollTo("#about")}>
              <BounceDiv>
                <img width="60" height="60" alt="" src="data:image/svg+xml;base64," />
              </BounceDiv>
            </button>


          </Fade>
        </div>
      </div>
    </div>
  )
}

export default Header
